.form__title {
  color: #111;
  border-bottom: 1px solid #C8DAE2;
  line-height: 46px;
  width: 100%; }

.form__body {
  margin-top: 20px;
  font-family: "Arial", "Microsoft YaHei", sans-serif;
  font-size: 14px;
  color: #333; }

.form__input {
  padding: 8px 10px;
  margin: 10px 0;
  border: 1px solid #999; }
  .form__input input {
    font-size: 14px;
    border: none;
    outline: none;
    color: #333;
    background: transparent; }
    .form__input input::-moz-placeholder {
      color: #999;
      font-weight: 400; }
  .form__input.input_disabled {
    background: #eee; }

.form__button {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
