  margin: 10px 0;
  font-size: 18px;
  color: #fff;
  background: #d45858;
  cursor: pointer; }
  .form__button:hover {
    background: #CA4141; }

.form__button.form__button_green {
  background: #008069; }
  .form__button.form__button_green:hover {
    background: #0db37f; }

.form__divider {
  position: relative;
  height: 200px;
  width: 0;
  border: 1px dashed #e6e6e6;
  margin: 20px 40px 0 40px; }



